.App {

}
.main_p{
  
  width:30%;
  margin-left:28px;
  padding: 14px;
  line-height:35px;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

body{
  background: #e0d6f1;
  font-family: 'Spectral', serif;
  font-size:28px;
}

a{
  text-decoration: none;
  color:black;letter-spacing: 12px; 
  word-break: break-word;
}

@media (max-width: 1200px) {
  
  .main_p{
  font-size:28px;
  width:84%;
  padding: 8%;
  line-height:35px;
  margin-left: 0px;
}
.not_main_p{
  font-size:28px;
  width:84%;
  padding: 8%;
  padding-top: 1%;
  padding-bottom: 1%;
  line-height:35px;
  margin-left: 0px;
}
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
